* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 30px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.js-list {
  grid-gap: 20px;
  grid-template-columns: repeat(3, 1fr);
  margin: 50px 0;
  padding: 0;
  list-style: none;
  display: grid;
}

.js-list li {
  height: 200px;
  text-align: center;
  grid-template-rows: 1fr auto;
  display: grid;
}

.js-list img {
  width: 100%;
  max-height: 200px;
  object-fit: cover;
  border-radius: 10px;
  display: block;
}

.js-list h2 {
  margin: 10px 0;
  font-size: 18px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 20px;
}

button:hover {
  background-color: #0056b3;
}

div {
  text-align: center;
}

input {
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 30px;
  padding: 10px;
}

.js-list-rep {
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.js-list-rep li {
  width: calc(50% - 10px);
  margin-bottom: 20px;
}

.js-list-rep a {
  color: currentColor;
  text-decoration: none;
}

.js-list-rep article {
  border: 1px solid #ccc;
  border-radius: 5px;
  flex-direction: column;
  padding: 10px;
  display: flex;
}

.js-list-rep img {
  width: 100%;
  max-height: 200px;
  object-fit: cover;
  border-radius: 5px;
  margin-bottom: 10px;
}

.js-list-rep h2 {
  margin: 0 0 5px;
  font-size: 18px;
}

.js-list-rep p {
  margin: 0;
  font-size: 14px;
}

.spinner-wrapper {
  align-items: center;
  display: flex;
}

.spinner-wrapper .spinner {
  margin-right: 10px;
}

.btn-load-more[disabled], .search-btn[disabled] {
  background-color: gray;
}

